.gitHub {
    position: fixed;
    right: 6rem;
    top: 1rem;
    width: 3.1rem;
    padding-left: 0.05rem;
    background-color: var(--two);
    border-radius: 0.1em;
    z-index: 100;
    box-shadow: 0.1em 0.1em 0.15em rgba(0, 0, 0, 0.5);
}

.gitHub.can2GH {
    color: #2CE1FB;
    background-color: #014877;
    border: 1px solid #014877;
}

.gitHub.can3GH {
    color: var(--cockpitBG);
    background-color: var(--cockpitText);
    padding-right: 0.1rem;
}

.linkedIn {
